<template>
  <b-row class="m-0">
    <b-col cols="12">
      <b-row class="bond-paper">
          <b-col  v-for="(ticket, index) in tickets" cols="6">
            <div :id="'ticket-'+ticket.ticket_number">
              <span class="ticket-number">{{ticket.ticket_number}}</span>
              <img :src="assets_path.DEFAULT_URL+'TICKET_TEMPLATE.png'" style="width:100%;border:1px solid #000;" />
              <qrcode-vue :value="ticket.code" :size="size" level="H" style="" class="qr-code-holder"/>
            </div>
          </b-col>
      </b-row>
    </b-col>
  </b-row>
</template>

<script>
    import {client_details,assets_path} from '@/config';
    import Loading from '@/views/widgets/Loading.vue';
    import SwalMixin from '@/views/mixins/Swal.js'
    import QrcodeVue from 'qrcode.vue'
    import html2canvas from 'html2canvas';

    export default {
        name: 'Login',
        mixins:[SwalMixin],
        components: {Loading,QrcodeVue},
        data(){
            return{
              value: 'https://example.com',
              size: 115,
              form:{
                name:'',
                qty:''
              },
              assets_path:{},
              tickets:[],
              download:false,
              is_loading:false,
            }
        },
      watch:{
      },
        created() {
          this.assets_path = assets_path;
          this.get();
        },
        methods:{

          get(){
            let id = this.$route.params.id;
            this.axios.get('ticket/get',{params:{id:id}}).then(response => {
              this.tickets = response.data.tickets;

            }).catch(error => console.log(error));
          },
        }
    }
</script>
<style>
  html,body{
    background:#fff
  }
.qr-code-holder{
  position: absolute;
  top:31px;
  right:46px
}
  .ticket-number{
    font-size: 27px;
    position: absolute;
    top: 35px;
    left: 220px;
    color: #000;
  }
  .bond-paper{
    width: 11in;
  }
</style>
<style type="text/css" media="print">
  @page { size: landscape; }
</style>
